.profileWrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 20px;
    padding-bottom: 20px;
}

.link {
    text-decoration: none;
    border: 1px solid #635D80;
    box-sizing: border-box;
    border-radius: 2px;
    padding: 2px 4px;
}

